(function () { 'use strict';
  if (typeof $Radi === 'undefined') { console.warn('[Radi.js DEV] Err: Radi.js package not found'); return false; }

  const LOGO = 'data:image/png;base64,';

  const { r, l, list, component, mount } = $Radi;

  mount(
    r('style',
      `.raddebug-wrap {
        position: fixed;
        top: 10px;
        right: 10px;
        bottom: 10px;
        width: 260px;
        max-width: 100%;
        z-index: 10000;
        background-color: rgba(72, 59, 106, 0.97);
        color: #fff;
        border-radius: 6px;
        padding: 14px 16px;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: auto;
        font-family: Tahoma;
        font-size: 16px;
      }
      .raddebug-body {
        width: 100%;
      }
      .raddebug-logo {
        display: block;
        margin: 8px 0 14px 0;
        text-align: center;
      }
      .raddebug-component {
        display: block;
        margin-top: 10px;
        background-color: rgba(44, 37, 64, 0.7);
        border-radius: 6px;
        line-height: 130%;
      }
      .raddebug-component > strong {
        display: block;
        cursor: pointer;
        padding: 5px 10px;
        border-radius: inherit;
        font-size: 14px;
        font-weight: normal;
      }
      .raddebug-component > strong > i {
        opacity: 0.2;
      }
      .raddebug-component > strong:hover {
        background-color: #2d273e;
      }
      .raddebug-component > ul {
        display: block;
        margin: 0;
        padding: 0 10px 10px 10px;
        font-size: 14px;
        list-style: none;
      }
      .raddebug-component ul li {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: #ae94c5;
      }
      .raddebug-component ul li > strong {
        display: inline;
        padding-left: 6px;
        color: #e2d7ff;
        font-weight: normal;
      }
      .raddebug-state-actor {
        display: block;
        margin-bottom: 10px;
      }
      .raddebug-state-actor button {
        display: block;
        width: 100%;
        margin-top: 10px;
        cursor: pointer;
      }`
    ),
    document.head
  );

  var stateActor = component({
    view: function() {
      return r('div.raddebug-state-actor',
        'State Actor: ',
        cond(
          l(!this.paused),
          r('button', { onclick: function () { this.paused = true; $Radi.freeze() } }, '■ Pause state')
        ).else(
          r('button', { onclick: function () { $Radi.unfreeze(); this.paused = false } }, '► Resume state')
        )
      )
    },
    state: {
      _radi_no_debug: true,
      paused: false
    },
    actions: {
      pause() {
        this.paused = true
        $Radi.freeze()
      },
      resume() {
        $Radi.unfreeze()
        this.paused = false
      }
    }
  });

  window.treeList = component({
    name: 'tree-list',
    // view: () => ( r('div', '>') )
    view: function() {
      return r('div',
        list(l(this.array), (item) => {
          return r('div', 'item')
        })
      )
    },
    props: {
      array: Array
    },
    actions: {
      onMount() {
        console.log('me mount', this.array)
      }
    }
  })

  var data = component({
    view: function() {
      return r('div',
        list(l(this.list), (component) => {
        // l(this.list).loop((component) => {
          return r('div.raddebug-component',
            r('strong',
              // { onclick: () => l(this.toggle(component.id)) },
              { onclick: () => this.toggle(component.id) },
              l((component.name) ? component.name : 'unnamed-' + component.id)
            ),
            cond(l(this.show === component.id),
              r('ul',
                cond(l((component.vars.state).length), r('strong', 'State')),
                list(l(component.vars.state), (item) => {
                  return r('li',
                    l(item.key),
                    ': ',
                    cond(
                      l(Array.isArray(item.value) && item.value !== null),
                      // new treeList().props({ array: l(item.value) })
                      // r('strong', 'Array of ', l((item.value).length), ' items')
                    ).else(
                      r('strong', l(item.value))
                    )
                  );
                }),
                cond(l((component.vars.props).length), r('strong', 'Props')),
                list(l(component.vars.props), (item) => {
                  return r('li',
                    l(item.key),
                    ': ',
                    cond(
                      l(Array.isArray(item.value) && item.value !== null),
                      r('strong', 'Array of ', l((item.value).length), ' items')
                    ).else(
                      r('strong', l(item.value))
                    )
                  );
                }),
                cond(l((component.vars.actions).length), r('strong', 'Actions')),
                list(l(component.vars.actions), (item) => {
                  return r('li',
                    l(item.key),
                    ': ',
                    r('button', {
                      style: {
                        cursor: 'pointer'
                      },
                      onclick: () => { item.value() }
                    }, 'trigger')
                  );
                })
              )
            )
          );
        })
      );
    },
    state: {
      _radi_no_debug: true,
      show: null,
      list: []
    },
    actions: {
      onMount() {
        setInterval(() => {
          this.list = this.loadList($Radi.activeComponents);
        }, 10)
      },
      loadList(cp) {
        var comp = []
        for (var ii = 0; ii < cp.length; ii++) {
          // Do not debug the debugger
          if (!cp[ii]._radi_no_debug) {
            var arr = {
              id: cp[ii].$id,
              name: cp[ii].$name,
              vars: {
                state: [],
                props: [],
                actions: []
              }
            }
            for (var nn in cp[ii].$state) {
              arr.vars.state.push({key: nn, value: cp[ii][nn]})
            }
            for (var nn in cp[ii].$props) {
              arr.vars.props.push({key: nn, value: cp[ii][nn]})
            }
            for (var nn in cp[ii].$actions) {
              arr.vars.actions.push({key: nn, value: cp[ii][nn]})
            }
            comp.push(arr)
          }
        }

        // setTimeout(() => {
        //   this.loadList(radi.activeComponents)
        // }, 1000)
        return comp;
      },
      toggle(id) {
        console.log(id)
        this.show = (this.show === id) ? null : id
      }
    }
  });

  mount(
    r('div.raddebug-wrap',
    r('div.raddebug-body',
      r('div.raddebug-logo',
        r('img', {
          src: LOGO,
          width: 107
        }),
      ),
      r('div', new stateActor()),
      r('div', new data())
    ),
    ),
    document.body
  );

})();
